@keyframes fade-in {
  from {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  to {
    display: block;
    opacity: 1;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1 {
  font-family: var(--font-antique);
  font-weight: 500;
  color: var(--smokeybrown);
  text-align: center;
}

h2 {
  font-family: var(--font-proxima);
  font-weight: 700;
  color: var(--smokeybrown);
  font-size: 3.5rem;
  text-align: center;
}

h3 {
  font-family: var(--font-proxima);
  font-weight: 700;
  color: var(--goldenyellow);
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 3.8125rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 15.3125rem;
  }
}

h3 {
  font-size: 1.375rem;
}
@media (min-width: 71.875em) {
  h3 {
    font-size: 2.3125rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --lightyellow: hsl(34, 57%, 60%);
  --dawnyellow: hsl(34, 56%, 54%);
  --goldenyellow: hsl(35, 47%, 50%);
  --darkyellow: hsl(40, 43%, 40%);
  --smokeybrown: hsl(20, 51%, 10%);
}

:root {
  --font-proxima: "proxima-nova", sans-serif;
  --font-antique: "antique-olive", sans-serif;
}

.container {
  padding: 0 1.4375rem;
  max-width: 103.75rem;
  margin: 0 auto 2.9375rem;
}
.container .header__toggle {
  margin: 1.3125rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 2.5rem 8.125rem 0;
  }
}

.card {
  padding: 1.25rem;
}

.button__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--dawnyellow);
}

.header {
  z-index: 1;
}
.header.open .overlay {
  display: block;
  animation: fade-in 300ms ease-in-out forwards;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 3.75rem;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(var(--darkyellow), transparent);
}
@media (min-width: 40em) {
  .header {
    margin-bottom: 2.75rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  margin-top: 0.9375rem;
  height: 2.625rem;
  display: flex;
}
.header__navigation {
  display: inline-block;
}
.header__toggle > span {
  display: block;
  width: 1.6875rem;
  height: 0.1875rem;
  background-color: var(--goldenyellow);
  transition: all 300ms ease-in-out;
  transform-origin: -0.5px 1px;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.4375rem;
}

.myNav {
  display: block;
  position: relative;
  height: 3.75rem;
}
.myNav ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
}
.myNav li:not(:last-child) {
  margin-right: 1.875rem;
}
.myNav a.underline-effect {
  font-family: var(--font-proxima);
  font-size: 1rem;
  font-weight: 400;
  color: var(--goldenyellow);
  text-decoration: none;
  padding: 0.3125rem 0 1.0625rem;
}

.underline-effect {
  display: inline-block;
  position: relative;
}

.underline-effect::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.125rem;
  background-color: var(--goldenyellow);
  transition: width 0.25s ease-out;
}

.underline-effect:hover::before {
  width: 100%;
}

.landing {
  background-color: whitesmoke;
}
.landing img {
  width: 352px;
  height: auto;
  transform: translateX(-5.5px);
  position: absolute;
}
.landing__circle-bg {
  width: 340px;
  height: 340px;
  background-color: var(--darkyellow);
  border-radius: 50%;
  position: absolute;
  display: inline-block;
}
.landing__circle-bg :hover {
  background-color: aqua;
}
.landing p {
  font-family: var(--font-proxima);
  font-weight: 400;
  font-size: 5rem;
  color: var(--dawnyellow);
}